<template>
  <div class="month-page">
    <h2>Альбом за {{ month }}</h2>
    <div class="gallery">
      <div class="photo" v-for="(media, index) in mediaItems" :key="index">
        <img 
          v-if="media.type === 'image'" 
          :src="media.src" 
          :alt="'Фото ' + (index + 1)"
          @click="openModal(media)">
        <video v-if="media.type === 'video'" controls>
          <source :src="media.src" type="video/mp4">
          Ваш браузер не поддерживает видео.
        </video>
      </div>
    </div>

    <div id="modal" class="modal" v-if="isModalOpen" @click="closeModal">
      <span class="close" @click="closeModal">&times;</span>
      <img class="modal-content" id="modal-image" :src="modalImage">
    </div>
  </div>
</template>

<script>
export default {
  props: ['month'],
  data() {
    return {
      mediaItems: [],
      isModalOpen: false,
      modalImage: ''
    };
  },
  created() {
    this.loadMedia();
  },
  watch: {
    month: 'loadMedia' // Отслеживание изменений пропса month
  },
  methods: {
    loadMedia() {
      // Загружаем изображения
      const imageContext = require.context('@/assets/Img', true, /\.(png|jpe?g|gif)$/);
      const images = imageContext.keys()
        .filter(file => file.includes(this.month)) // Проверка на соответствие имени месяца
        .map((file) => {
          return {
            src: imageContext(file),
            type: 'image',
            description: `Описание фото ${file}`
          };
        });

      // Загружаем видео
      const videoContext = require.context('@/assets/Img', true, /\.(mp4)$/);
      const videos = videoContext.keys()
        .filter(file => file.includes(this.month))
        .map((file) => {
          return {
            src: videoContext(file),
            type: 'video',
            description: `Описание видео ${file}`
          };
        });

      // Объединяем изображения и видео
      this.mediaItems = [...images, ...videos];
    },

    openModal(media) {
      // Устанавливаем URL выбранного медиа для отображения в модальном окне
      if (media.type === 'image') {
        this.modalImage = media.src;
        this.isModalOpen = true;
      }
    },

    closeModal() {
      this.isModalOpen = false;
    }
  }
};
</script>

