       <span class="about__text">Вес: 3390г</span>
      </div>
      <div class="age">Место рождения: г. Самара</div>
      <div class="age">Нам: {{ age.months }} мес.</div> 
    </div>
    <div class="about__container-v">
      <h1>Наши достижения</h1>

<table>
    <thead>
        <tr>
            <th>Навыки</th>
            <th>Возраст</th>
        </tr>
    </thead>
    <tbody>
        <tr>
            <td>Первый раз перевернулась на животик</td>
