<script>
export default {
  name: 'AboutPage',
  data() {
    return {
      birthDate: '2024-10-10', // Дата рождения
      age: { years: 0, months: 0 }, // Переменная для возраста
    };
  },
  created() {
    this.age = this.calculateAge(this.birthDate); // Вычисляем возраст при создании компонента
  },
  methods: {
    calculateAge(birthDate) {
      const today = new Date();
      const birth = new Date(birthDate);

      let years = today.getFullYear() - birth.getFullYear();
      let months = today.getMonth() - birth.getMonth();

      // Если день рождения еще не наступил в текущем году, уменьшаем возраст на 1 год
      if (months < 0 || (months === 0 && today.getDate() < birth.getDate())) {
        years--;
        months += 12; // Прибавляем 12 месяцев, если не достигли дня рождения
      }

      // Если мы уже в этом месяце, но день рождения еще не наступил, уменьшаем месяц
      if (today.getDate() < birth.getDate()) {
        months--;
      }

      return { years, months }; // Возвращаем объект с годами и месяцами
    }
  }
}
</script>

