import { createRouter, createWebHistory } from 'vue-router';
import MonthPage from '../components/MonthPage.vue';
import AboutPage from '../components/AboutPage.vue';

const routes = [
  { path: '/0month', component: MonthPage, props: { month: '0 мес' } },
  { path: '/1month', component: MonthPage, props: { month: '1 мес' } },
  { path: '/2month', component: MonthPage, props: { month: '2 мес' } },
  { path: '/3month', component: MonthPage, props: { month: '3 мес' } },
  { path: '/about', component: AboutPage },
  { path: '/', redirect: '/0month' } // исправленный редирект
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;
